<template>
    <div class="container">
        <TopButton title="" />
        <div class="order-complete-container">
            <div class="order">
                <h1> !</h1>
                <p> .</p>
                <p> : 3548798754684241</p>
                <button class="btns"></button>
                <button @click="goToMain" class="btns"> </button>
            </div>
            <div class="complete">
                <p>
                      : <strong>{{ totalAmount.toLocaleString() }} </strong>
                </p>
            </div>
        </div>
    </div>
</template>
<script>
import TopButton from '@/components/TopButton.vue';
export default {
    name: 'OrderComplete',
    components: { TopButton },
    data() {
        return {
            totalAmount: 0 //    
        };
    },
    created() {
        // query     
        const totalQuery = this.$route.query.total;
        if (totalQuery && !isNaN(totalQuery)) {
            this.totalAmount = parseInt(totalQuery, 10); //   
        } else {
            console.warn('    .');
            this.totalAmount = 0; //  
        }
    },
    methods: {
        goToMain() {
            this.$router.push('/'); //   
        }
    }
};
</script>
<style scoped>
.order-complete-container {
    text-align: center;
    margin: 50px auto;
    max-width: 600px;
    font-family: Arial, sans-serif;
}

h1 {
    font-size: 24px;
}

p {
    margin: 10px 0;
}

.btns {
    background-color: #999;
    padding: 10px 20px;
    cursor: pointer;
    margin-top: 20px;
    font-size: 16px;
}
</style>
